@import "../../general.scss";

.navbar {
  height: 65px;
  // background: black;
  color: #f1faee;
}

.main-nav {
  display: flex;
  justify-content: center;
  max-width: 1300px;
  margin: auto;
  height: 100%;
  width: 400px;
  border-bottom-left-radius: 0.7rem;
  border-bottom-right-radius: 0.7rem;
  align-items: center;
  background-color: #457b9d;
}

.main-nav .links {
  display: flex;
  gap: 3rem;
}

.main-nav .links li {
  list-style-type: none;
  font-size: 1.1rem;
  font-weight: 300;
  cursor: pointer;
}

.main-nav .links .link {
  position: relative;
  // color: $text-lightgray;
  color: #f1faee;
  text-decoration: none;
}

.main-nav .links .link:after {
  content: "";
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 1px;
  bottom: 0;
  left: 0;
  // background-color: $text-lightgray;
  background-color: #f1faee;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}

.main-nav .links .link:hover:after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

.btn-mobile-nav {
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  padding: 0.5rem;
  color: $primary1;
  background-color: $background-gray;
  border: 2px solid $primary1;
  margin: 1rem;
  border-radius: 0.5rem;
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;

  &:hover {
    background-color: $primary1;
    color: $background-gray;
  }
}
